

.vue-skeletor {
  position: relative;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.12); }
.vue-skeletor:not(.vue-skeletor--shimmerless):after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  background-image: -webkit-gradient(linear, left top, right top, from(rgba(255, 255, 255, 0)), color-stop(rgba(255, 255, 255, 0.3)), to(rgba(37, 22, 22, 0)));
  background-image: linear-gradient(90deg, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.3), rgba(37, 22, 22, 0));
  -webkit-animation: shimmer 1.5s infinite;
          animation: shimmer 1.5s infinite;
  content: ''; }
.vue-skeletor--rect, .vue-skeletor--circle {
  display: block; }
.vue-skeletor--circle {
  border-radius: 50%; }
.vue-skeletor--pill, .vue-skeletor--text {
  border-radius: 9999px; }
.vue-skeletor--text {
  line-height: 1;
  display: inline-block;
  width: 100%;
  height: inherit;
  vertical-align: middle;
  top: -1px; }
@-webkit-keyframes shimmer {
  100% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%); } }
@keyframes shimmer {
  100% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%); } }
.collapse:not(.show) {
    visibility: hidden;
}
.collapsing{
    height: 0;
    overflow: hidden;
    -webkit-transition: all 300ms ease-in-out;
    transition: all 300ms ease-in-out;
}
.translate-enter-active,.translate-leave-active{
    -webkit-transition: all 250ms linear;
    transition: all 250ms linear;
}
.translate-enter-from,.translate-leave-to{
    top: 0 !important;
}
.translate-enter-from,.translate-leave-to{
    visibility: hidden;
    opacity: 0;
    -webkit-transform: scale(0.96) ;
            transform: scale(0.96) ;
}
.translate-enter-to,.translate-leave-from {
    top: 60px !important;
}
.translate-enter-to,.translate-leave-from {
    visibility: visible;
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}
.fade-enter-active,.fade-leave-active{
    -webkit-transition: all 150ms linear;
    transition: all 150ms linear;
}
.fade-enter-from,.fade-leave-to{
    visibility: hidden;
    opacity: 0;
}
.fade-enter-to,.fade-leave-from {
    visibility: visible;
    opacity: 1;
}
